  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

h1,
h2,
h3 {
  font-family: "Playfair Display";
  font-weight: 500;
}

p {
  font-family: "Roboto";
}

header {
  width: 100%;
  position: relative;
  display: flex;
  flex-direction: column;
  gap: 12px;
  overflow: hidden;
  padding-bottom: 50px;
}

header section {
  display: flex;
  flex-direction: column;
  gap: 20px;
  background-color: white;
}

.headerAccent {
  position: absolute;
  width: 100%;
  height: 600px;
  background-color: rgb(0, 123, 160);
  transform: skew(-33deg) rotate(-33deg) translateY(-325px);
  box-shadow: 2px 2px 10px 2px rgba(0, 0, 0, 0.2);
  z-index: -1;
}

.photoContainer {
  position: relative;
  padding-top: 15px;
  display: flex;
  justify-content: center;
  width: max-content;
  margin: 0 auto;
}

header img {
  max-width: 90vw;
  width: 340px;
  height: auto;
  box-shadow: 2px 2px 10px 2px rgba(0, 0, 0, 0.2);
}

h1 {
  position: absolute;
  font-size: 3.6rem;
  color: white;
  text-shadow: 1px 1px 2px black;
  bottom: 15px;
  left: 15px;
}

section {
  padding: 20px;
  display: flex;
  flex-direction: column;
  gap: 25px;
}

header p {
  font-size: 1.5rem;
  color: rgb(71, 71, 71);
}

ul {
  display: flex;
  flex-direction: row;
  justify-content: center;
  list-style-type: none;
  gap: 20px;
}

svg {
  width: 35px;
  height: auto;
}

a {
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

h2 {
  text-align: center;
  font-size: 2.8rem;
}

.projectCards {
  display: grid;
  grid-template-columns: 1fr;
  row-gap: 56px;
  column-gap: 28px;
}

.project {
  box-shadow: 1px 1px 6px 2px rgba(0, 0, 0, 0.2);
}

.screenshot {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 350px;
  font-family: "Playfair Display";
  font-size: 2.5rem;
  padding: 30px;
  color: white;
  text-align: center;
}

.purple {
  background-color: rgb(138, 98, 156);
}

.orange {
  background-color: orange;
}

.green {
  background-color: rgb(38, 119, 38);
}

.magenta {
  background-color: rgb(175, 98, 162);
}

.blue {
  background-color: rgb(41, 133, 194);
}

.red {
  background-color: rgb(179, 71, 71);
}

h3 {
  font-size: 1.5rem;
}

.info {
  display: flex;
  flex-direction: column;
  gap: 20px;
  padding: 20px;
  padding-bottom: 80px;
}

.info a {
  padding-left: 10px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.info a:link,
.info a:visited {
  color: black;
}

.icons {
  display: flex;
  flex-direction: row;
  gap: 5px;
}

.icons svg {
  width: 30px;
}

.info p {
  font-size: 1.3rem;
  color: rgb(77, 77, 77);
}

footer section {
  background-color: rgb(47, 136, 190);
  color: white;
  padding-bottom: 100px;
}

.contactInfo {
  display: flex;
  flex-direction: column;
  gap: 40px;
}

.contacts {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

footer p {
  font-size: 1.4rem;
  font-weight: 300;
}

footer ul {
  gap: 40px;
}

footer svg {
  width: 45px;
}

.phone svg,
.email svg {
  display: none;
}


footer img {
  width: 100%;
  height: auto;
  margin-bottom: -4px;
}

