  }
  .aboutSect p {
    font-size: 1.4rem;
  }
  h1 {
    width: max-content;
    right: -225px;
    left: auto;
    bottom: auto;
    top: 10px;
    font-size: 3rem;
    }
  .headerAccent {
    transform: skew(-20deg) rotate(-20deg) translateY(-400px);
  }
  header ul {
    display: none;
  }
  h2 {
    font-weight: 400;
    text-align: start;
  }
  section {
    padding: 50px;
  }
  .projectCards {
    grid-template-columns: 1fr 1fr;
    column-gap: 20px;
    row-gap: 30px;
  }
  ul {
    justify-content: start;
  }
  .screenshot {
    font-size: 2.1rem;
    height: 220px;
  }
  .info {
    padding-bottom: 40px;
    height: max-content;
  }
  .info a {
    padding-left: 0px;
  }
  h3 {
    font-size: 1.2rem;
  }
  .icons svg {
    width: 25px;
  }
  .phone,
  .email {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 10px;
  }
  .phone svg,
  .email svg {
    margin-top: 2px;
    display: block;
    fill: white;
    width: 25px;
  }
  .contactInfo {
    width: 50%;
  }
  footer p {
    font-size: 1.4rem;
  }
  footer {
    position: relative;
  }
  footer img {
    position: absolute;
    width: 40%;
    right: 5%;
    top: 5%;
    max-height: 450px;
    object-fit: contain;
  }
}

@media (min-width: 700px) {
  h1 {
    right: -275px;
  }
}
@media (min-width: 725px) {
  h1 {
    right: -300px;
  }
}
@media (min-width: 825px) {
  h1 {
    right: -325px;
    font-size: 3.3rem;
  }
}
@media (min-width: 900px) {
  h1 {
    right: -350px;
  }
}

@media (min-width: 992px) {
  .headerAccent {
    transform: skew(-10deg) rotate(-10deg) translateY(-270px);
