  .photoContainer {
    width: 30%;
    min-width: 500px;
    margin-left: calc(min(4%, 120px));
    margin-top: 40px;
    margin-right: -5px;
  }
  h1 {
    top: auto;
    right: auto;
    bottom: 50px;
  }
  .aboutSect {
    display: flex;
    justify-content: space-between;
    margin: 0;
    margin-top: 120px;
    margin-right: calc(min(4%, 120px));
    padding: 30px;
    padding-left: 50px;
    padding-top: 50px;
    min-height: 350px;
  }
  .aboutSect h2 {
    text-align: start;
    font-size: 2.4rem;
  }
  .aboutSect p {
    font-size: 1.2rem;
  }
  header ul {
    margin-top: 25px;
    display: flex;
    justify-content: end;
  }
  header svg {
    width: 30px;
  }
  section {
    padding-left: calc(min(5%, 100px));
    padding-right: calc(min(5%, 100px));
  }
  .projectCards {
    grid-template-columns: 1fr 1fr 1fr;
    column-gap: calc(min(3%, 50px));
    row-gap: 70px;
  }
  .screenshot span {
    font-size: 2.3rem;
  }
  h2 {
    font-size: 3rem;
  }
  h3 {
    font-size: 1.7rem;
  }
  .icons svg {
    width: 32px;
  }
  .screenshot {
    font-size: 3rem;
    height: 360px;
  }
  .info p {
    font-size: 1.5rem;
  }
  footer img {
    width: 43%;
  }
}
